:root {
  box-sizing: border-box;
  font-size: 14px;
  line-height: 28px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: inherit;
}

html,
body {
  font-weight: 400;
  font-family: "Lato", Arial, sans-serif;
  font-size: inherit;
  line-height: inherit;
  scroll-behavior: smooth;
  color: #3d3d39;
  background-color: #f6f1ed;
}

a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
b {
  font-weight: inherit;
  font-size: inherit;
}

/* ----- menu media ----- */
body._lock {
  overflow: hidden;
}

@media (max-width: 995px) {
  .menu-burger {
    display: block;
    right: 0;
    position: relative;
    width: 3rem;
    height: 0.5rem;
    transition: all 0.3s ease 0s;
    margin-right: 5rem;
    z-index: 5;
  }

  .menu-burger span,
.menu-burger::before,
.menu-burger::after {
    position: absolute;
    left: 0;
    width: 3rem;
    height: 0.5rem;
    background-color: #fff;
    transition: all 0.3s ease 0s;
  }

  .menu-burger::before,
.menu-burger::after {
    content: "";
  }

  .menu-burger::before {
    top: -1rem;
  }

  .menu-burger::after {
    bottom: -1rem;
  }

  .menu-burger._active span {
    width: 0;
    height: 0;
  }

  .menu-burger._active::before {
    top: 0;
    transform: rotate(-45deg) translate(0px, 0);
  }

  .menu-burger._active::after {
    bottom: 0;
    transform: rotate(45deg) translate(0px, 0);
  }

  .menu-list {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    color: #ffffff;
    background-color: #3b3938;
    padding: 2rem;
    padding-top: 7rem;
    transition: left 0.3s ease 0s;
    overflow: auto;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .menu-list._active {
    left: 0;
  }

  .menu-item,
.menu-link {
    margin: 0;
    padding: 1rem;
    width: 100%;
    font-size: 1.7rem;
  }

  .menu-item:last-child,
.menu-link:last-child {
    padding-bottom: 0;
  }
}
/* ----- menu media end ----- */
/* ----- pc / touch ----- */
body._touch .menu-item {
  display: flex;
  align-items: center;
}
body._touch .menu-link {
  flex: 1 1 auto;
}

/* ----- pc / touch end ----- */
